import Vue from 'vue'
import Router from 'vue-router'
import Login from './views/Login.vue'
import Home from './views/Home.vue'
import FriendChat from './views/chat/FriendChat.vue'
import HrInfo from "./views/HrInfo";

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Login',
            component: Login,
            hidden:true
        }, {
            path: '/home',
            name: 'Home',
            component: Home,
            //  true  401login
            hidden: true,
            // 
            meta:{
                roles:['admin','user']
            },
            children:[
                {
                    path: '/chat',
                    name: '',
                    component: FriendChat,
                    hidden: true
                },
                {
                    path: '/hrinfo',
                    name: '',
                    component: HrInfo,
                    hidden: true
                }
            ]
        },
        {
            path: '*',
            redirect: '/home',
        }
    ]
})


